import React from "react";
class One extends React.Component {
  constructor(props) {
    super();
    this.state = {};
  }
  render() {
    return (
      <div
        style={{ width: "800px", height: "100%", backgroundColor: "#ffffff" }}
      >
        <h1>
          From One{" "}
          <button
            style={{ float: "right" }}
            onClick={() => this.props.handleClose("One")}
          >
            close
          </button>
        </h1>
        <div>Do anything </div>
      </div>
    );
  }
}
export default One;
