import React from "react";
import $ from "jquery";
import One from "./one";
import Two from "./two";
import Three from "./three";

export default class Menu extends React.Component {
  constructor() {
    super();
    this.state = {
      click: false,
      menuCall: ""
    };
  }
  handleMenuSlide = (call) => {
    const that = this;
    if ($(".parentMenuDiv").hasClass("expand")) {
      $(".parentMenuDiv").removeClass("expand");
      $("." + that.state.menuCall).removeClass("menu-icon-onclick");
      that.setState({ click: false, menuCall: "", edit: false });
      if (that.state.menuCall !== "" && that.state.menuCall !== call) {
        $("." + call).addClass("menu-icon-onclick");

        setTimeout(function () {
          $(".parentMenuDiv").addClass("expand");
          that.setState({ click: true, menuCall: call });
        }, 800);
      }
    } else {
      $(".parentMenuDiv").addClass("expand");
      that.setState({ click: true, menuCall: call });
      console.log(call);
      $("." + call).addClass("menu-icon-onclick");
    }
  };
  render() {
    return (
      <div className="parentMenuDiv">
        <div className="verticalBar" style={{ width: "60px" }}>
          <button
            className="One"
            style={{ width: "100%", height: "30px" }}
            onClick={() => this.handleMenuSlide("One")}
          >
            One
          </button>
          <button
            className="Two"
            style={{ width: "100%", height: "30px" }}
            onClick={() => this.handleMenuSlide("Two")}
          >
            Two
          </button>
          <button
            className="Three"
            style={{ width: "100%", height: "30px" }}
            onClick={() => this.handleMenuSlide("Three")}
          >
            Three
          </button>
        </div>
        {this.state.click ? (
          <div
            style={{
              width: "800px",
              backgroundColor: "white",
              float: "right",
              height: "100%"
            }}
          >
            {this.state.menuCall === "One" ? (
              <One handleClose={this.handleMenuSlide} />
            ) : null}
            {this.state.menuCall === "Two" ? (
              <Two handleClose={this.handleMenuSlide} />
            ) : null}
            {this.state.menuCall === "Three" ? (
              <Three handleClose={this.handleMenuSlide} />
            ) : null}
          </div>
        ) : null}
      </div>
    );
  }
}
